import React, { useState } from 'react'
import './navbar.css';
import { RiMenu3Line, RiCloseLine } from 'react-icons/ri';

const Menu = () => (
    <>
        <p><a href="#introduction">Introduction</a></p>
        <p><a href="#experiences">Experiences</a></p>
        <p><a href="#aboutme">About me</a></p>
        <p><a href="#hobbies">Hobbies</a></p>
    </>
)
const Navbar = () => {
    const [toggleMenu, setToggleMenu] = useState(false);
    return (
        <div className="beedle__navbar">
            <div className="beedle__navbar-links">
                <div className="beedle__navbar-links_container">
                    <Menu />
                </div>
            </div>
            <div className="beedle__navbar-menu">
                {toggleMenu
                    ? <RiCloseLine color="#fff" size={27} onClick={() => setToggleMenu(false)} />
                    : <RiMenu3Line color="#fff" size={27} onClick={() => setToggleMenu(true)} />}
                {toggleMenu && (
                    <div className="beedle__navbar-menu_container scale-up-center">
                        <div className="beedle_navbar-menu-container-links">
                            <Menu />
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}

export default Navbar
